'use client'
import React, { useState, useEffect, useRef } from 'react'
import { FaPaperPlane, FaUsers } from 'react-icons/fa'

const Communication = () => {
  const [messages, setMessages] = useState([])
  const [input, setInput] = useState('')
  const [username, setUsername] = useState('')
  const [connected, setConnected] = useState(false)
  const [users, setUsers] = useState([])
  const [error, setError] = useState('')
  const ws = useRef(null)
  const endRef = useRef(null)

  useEffect(() => {
    endRef.current?.scrollIntoView({ behavior: 'smooth' })
  }, [messages])

  const loadMessages = async () => {
    try {
      const res = await fetch('http://localhost:8080/api/chat/messages')
      if (res.ok) {
        const data = await res.json()
        setMessages(data.reverse())
      }
    } catch (err) {
      console.error('Failed to load messages:', err)
    }
  }

  const connect = (name) => {
    loadMessages()
    setError('')
    
    try {
      ws.current = new WebSocket(`ws://localhost:8080/chat?username=${encodeURIComponent(name)}`)
      
      ws.current.onopen = () => {
        setConnected(true)
        setError('')
        console.log('WebSocket connected')
      }
      
      ws.current.onmessage = (e) => {
        const data = JSON.parse(e.data)
        if (data.type === 'CHAT') setMessages(prev => [...prev, data])
        if (data.type === 'USERS') setUsers(data.users)
      }
      
      ws.current.onerror = (err) => {
        console.error('WebSocket error:', err)
        setError('Connection error. Make sure backend is running on port 8080.')
      }
      
      ws.current.onclose = () => {
        setConnected(false)
        console.log('WebSocket closed, reconnecting...')
        setTimeout(() => connect(name), 1000)
      }
    } catch (err) {
      console.error('Failed to create WebSocket:', err)
      setError('Failed to connect to chat server')
    }
  }

  const join = (e) => {
    e.preventDefault()
    if (username.trim()) connect(username)
  }

  const send = (e) => {
    e.preventDefault()
    if (!input.trim()) return
    
    if (ws.current && ws.current.readyState === WebSocket.OPEN) {
      ws.current.send(JSON.stringify({
        type: 'CHAT',
        username,
        content: input,
        timestamp: new Date().toISOString()
      }))
      setInput('')
    } else {
      console.log('WebSocket not connected')
    }
  }

  if (!username) {
    return (
      <div className='flex items-center justify-center min-h-screen bg-blue-50'>
        <div className='bg-white p-8 rounded-2xl shadow-xl w-96'>
          <h1 className='text-3xl font-bold text-center text-blue-700 mb-6'>Join Chat</h1>
          <form onSubmit={join}>
            <input
              type='text'
              placeholder='Enter your name'
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className='w-full px-4 py-3 border-2 border-blue-300 rounded-lg mb-4 text-black focus:outline-none focus:border-blue-500'
              required
            />
            <button className='w-full bg-blue-600 hover:bg-blue-700 text-white py-3 rounded-lg font-bold'>
              Join Chat
            </button>
          </form>
        </div>
      </div>
    )
  }

  return (
    <div className='flex h-screen bg-blue-50'>
      <div className='w-64 bg-white border-r p-4'>
        <div className='bg-blue-100 p-3 rounded-lg mb-4'>
          <FaUsers className='inline text-blue-600 mr-2' />
          <span className='font-bold text-black'>Online ({users.length})</span>
        </div>
        {users.map((u, i) => (
          <div key={i} className='flex items-center gap-2 p-2 mb-2 bg-green-50 rounded border-l-4 border-green-500'>
            <div className='w-2 h-2 bg-green-500 rounded-full'></div>
            <span className='text-black font-semibold'>{u}</span>
          </div>
        ))}
      </div>

      <div className='flex-1 flex flex-col'>
        <div className='bg-blue-600 p-4 text-white'>
          <h2 className='text-2xl font-bold'>Members Chat</h2>
          <p className='text-sm'>Welcome, {username}</p>
          <div className='mt-2 flex items-center gap-2'>
            <div className={`w-2 h-2 rounded-full ${connected ? 'bg-green-400' : 'bg-red-400'}`}></div>
            <span className='text-xs'>{connected ? 'Connected' : 'Connecting...'}</span>
          </div>
          {error && (
            <div className='mt-2 bg-red-500 text-white px-3 py-2 rounded text-xs'>
              {error}
            </div>
          )}
        </div>

        <div className='flex-1 overflow-y-auto p-4 bg-white'>
          {messages.length === 0 ? (
            <div className='text-center text-gray-500 mt-10'>
              <p>No messages yet. Start the conversation!</p>
            </div>
          ) : (
            messages.map((m, i) => (
              <div key={m.id || i} className='flex gap-3 mb-3'>
                <div className='w-10 h-10 rounded-full bg-blue-600 flex items-center justify-center text-white font-bold'>
                  {m.username[0].toUpperCase()}
                </div>
                <div className='flex-1 bg-gray-100 rounded-lg p-3'>
                  <div className='flex justify-between mb-1'>
                    <span className='font-bold text-blue-700'>{m.username}</span>
                    <span className='text-xs text-gray-500'>{new Date(m.timestamp).toLocaleTimeString()}</span>
                  </div>
                  <p className='text-black'>{m.content}</p>
                </div>
              </div>
            ))
          )}
          <div ref={endRef} />
        </div>

        <div className='bg-white border-t p-4'>
          <form onSubmit={send} className='flex gap-2'>
            <input
              type='text'
              placeholder={connected ? 'Type a message...' : 'Waiting for connection...'}
              value={input}
              onChange={(e) => setInput(e.target.value)}
              className='flex-1 px-4 py-3 border-2 border-blue-300 rounded-lg text-black focus:outline-none focus:border-blue-500'
              autoComplete='off'
            />
            <button 
              type='submit'
              disabled={!input.trim()}
              className='bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed text-white px-6 py-3 rounded-lg font-bold flex items-center gap-2'
            >
              <FaPaperPlane /> Send
            </button>
          </form>
          {!connected && (
            <p className='text-xs text-red-600 mt-2'> Not connected. Make sure Spring Boot is running on port 8080.</p>
          )}
        </div>
      </div>
    </div>
  )
}

export default Communication